export default {
  name: 'valorant',
  components: {
    VueKofi,
    D3Network,
  },
  data () {
    return {
      nodes: [],
      links: [],
      options: {
        force: 500,
        size: {w: 3000, h: 2500},
        nodeSize: 20,
        nodeLabels: true,
        canvas: false,
      },

      selected: -1,

      search: null,
      searchItems: [],
      playerInfo: {},
      transferTeams,

      oldTransform: {
        scale: 1,
      },
      currentTransform: {
        scale: 1,
        translateX: 0,
        translateY: 0,
      }
    }
  },
  methods: {
    handleDrag(event) {
      let target = event.target
      this.currentTransform.translateX += event.dx
      this.currentTransform.translateY += event.dy

      // translate the element
      this.applyCurrentTransform(target)
    },
    applyCurrentTransform (target) {
      const {translateX, translateY, scale} = this.currentTransform

      target.style.webkitTransform =
        target.style.transform =
          'translate(' + translateX + 'px, ' + translateY + 'px)' + 'scale(' + scale + ')'
    },
    highlightNode(event, node) {
      this.search = this.nodes[node.index].name
      if (this.selected !== -1) this.nodes[this.selected]._cssClass = ''
      this.nodes[node.index]._cssClass = 'selection'
      
      this.selected = node.index
      this.$set(this.currentTransform, 'translateX', window.screen.width * 0.6 - node.x)
      this.$set(this.currentTransform, 'translateY', window.screen.height * 0.35 - node.y)
      this.applyCurrentTransform(this.$refs.interactBracket)
      // Use this timeout so that the player info card will animate when a new player is selected.
      // this.selected = -1
      // setTimeout(() => {
      //   this.selected = node.index
      // }, 250)
    },
  },
  watch: {
    search () {
      for (let i in this.nodes) {
        if (this.nodes[i].id === this.search) {
          this.highlightNode({}, this.nodes[i])
          break
        }
      }
    }
  },
  created () {
    var link = document.querySelector("link[rel*='icon']") || document.createElement('link')
    link.type = 'image/x-icon'
    link.rel = 'shortcut icon'
    link.href = 'valorant.png'
    document.getElementsByTagName('head')[0].appendChild(link)
  },
  mounted () {
    this.nodes = Object.keys(transfersGraph).map(v => ({id: v, name: v}))
    this.links = Object.entries(transfersGraph).map(([k, v]) => v.teammates.map(p => ({sid: k, tid: p}))).flat()

    this.searchItems = Object.keys(transfersGraph)
    this.playerInfo = transfersGraph

    var self = this
    interact('#interact-bracket')
      .draggable({
        //inertia: true,
        onmove: this.handleDrag
      })
      .gesturable({
        listeners: {
          move (event) {
            self.currentTransform.scale = Math.pow(event.scale, 0.5) * self.oldTransform.scale

            self.handleDrag(event) // this will handle the drag as well as apply the current transform.
          },
          end (event) {
            self.oldTransform.scale *= Math.pow(event.scale, 0.5)
          }
        }
      })
  }
}
</script>

