  background-color: #eee;
}
.selection {
  fill: rgba(255, 196, 0, 0.911) !important;
  stroke: rgb(255, 0, 0) !important;
}

.info-box {
  position: absolute;
  top: 20px;
  left: 50px;
  width: 30vw;
  height: 300px;
}

.player-history {
  position: absolute;
  top: 350px;
  left: 50px;
  width: 30vw;
  max-height: 60vh;
  height: 500px;
  overflow: auto;
}
.no-selection {
  height: 100px !important;
}

.v-card__text {
  color: black !important;
}
</style>
